#kanban {
	background-image: url("https://magdeleine.co/wp-content/uploads/2019/12/8253400061_63c48f12ab_o-1400x927.jpg");
	background-attachment: fixed;
	background-size: cover;
	width: 100%;
	padding-left: 36px;
	position: relative;
}

#kanban .button {
	margin-bottom: 8px;
    width: 150px;
}

#kanban .card {
    margin-top: 12px;
}

#kanban .input-text {
    margin-bottom: 8px;
}

#kanban #kanban-first-row {
	align-items: center;
	display: flex;
	flex-direction: row;
	justify-content: center;
	padding-top: 24px;
}

#kanban #kanban-first-row #upload-Kanban-plugin {
    margin-left: 24px;
}

#kanban #kanban-first-row .css-q0jhri-MuiInputBase-root-MuiInput-root {
    color: rgba(200, 200, 200, 0.8);
}

#kanban #kanban-board {
	display: flex;
	flex-direction: row;
	height: 70%;
	margin-top: 24px;
	overflow: auto;
	position: relative;
	width: 100%;
}

#kanban #kanban-board .column {
	flex: 0 0 auto;
	margin-left: 16px;
	position: relative;
	width: 300px;
}

body::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}
body::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0); /* color of the tracking area */
}
body::-webkit-scrollbar-thumb {
  background-color: rgba(150, 150, 150, 0.5); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 1px solid rgba(150, 150, 150, 0.75); /* creates padding around scroll thumb */
}

#kanban #kanban-board::-webkit-scrollbar {
  height: 12px; /* width of the entire scrollbar */
}
#kanban #kanban-board::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0); /* color of the tracking area */
}
#kanban #kanban-board::-webkit-scrollbar-thumb {
  background-color: rgba(150, 150, 150, 0.5); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 1px solid rgba(150, 150, 150, 0.75); /* creates padding around scroll thumb */
}

